import { Typography } from '@material-ui/core';
import React from 'react';
import { appRoutes } from '../../../App';
import { AppLayout } from '../../../layouts';
import { AdminLink, AdminMainContainer, AdminContent } from './styled';

type AdminMainProps = {}

export const AdminMain: React.FC<AdminMainProps> = (props) => {
  return (
    <AppLayout>
      <AdminMainContainer>
        <Typography className="fix" variant="h3"> </Typography>

        <AdminContent>
          <AdminLink to={appRoutes.AdminUsers}></AdminLink>
          <AdminLink to={appRoutes.Practices}></AdminLink>
          <AdminLink to={appRoutes.AdminLessons}></AdminLink>
          <AdminLink to={appRoutes.AdminInstruction}></AdminLink>
        </AdminContent>
      </AdminMainContainer>
    </AppLayout>
  );
};